import React, { Component } from 'react';
import '../App.css';
import { Nav, Navbar, DropdownButton, Dropdown } from 'react-bootstrap';
import { user_token,user_token_decoded } from '../support/Constance';
import { get } from '../service/service';
import { MDBIcon } from "mdbreact"

class SingnedInLinks extends Component {
    constructor(props) {
        super(props);

        this.toggleNavbar = this.toggleNavbar.bind(this);
        this.state = {
            get_user: [],
            isOpan: false
        };
    }
    toggleNavbar() {
        this.setState({
            isOpan: !this.state.collapsed
        });
    }

    render_type = () => {
        console.log(user_token_decoded)
        let render_user
        switch (user_token_decoded.user_type) {
            case "1":
                render_user =
                    <div>
                        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" >
                            <Navbar.Brand href="/homereal"></Navbar.Brand>
                            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                            <Navbar.Collapse id="responsive-navbar-nav">
                                <Nav className="mr-auto">
                                </Nav>
                                <Nav>
                                    <div className="dropdown">
                                        <button className="dropbtn"><MDBIcon icon="angle-down" /></button>
                                        <div className="dropdown-content">
                                            <a href="/location_gym"></a>
                                            <a href="/add_course"></a>
                                            <a href="/add_course_sup"></a>
                                            <a href="/chack_course"></a>
                                            <a href="/add_promotion"></a>

                                            
                                        </div>
                                    </div>
                                    <Nav.Link href="/show_user" ></Nav.Link>
                                    <Nav.Link href="/register"></Nav.Link>
                                    {/* <Nav.Link href="/add_product"></Nav.Link>
                                    <Nav.Link href="/show_product"></Nav.Link>
                                    <Nav.Link href="/add_promotion"></Nav.Link> */}
                                    <Nav.Link href="/StaticService"></Nav.Link>

                                    <div className="dropdown">
                                        <button className="dropbtn">-<MDBIcon icon="angle-down" /></button>
                                        <div className="dropdown-content">
                                            <a href="/bill"></a>
                                            <a href="/add_income">-</a>
                                            <a href="/check_income"></a>
                                            <a href="/Income">-</a>
                                        </div>
                                    </div>
                                    <Nav.Link href="/user"></Nav.Link>
                                    <Nav.Link  onClick={this.logOut.bind(this)}></Nav.Link>
                                </Nav>
                            </Navbar.Collapse>
                        </Navbar>
                    </div>
                break;

            case "2":
                render_user =
                    <div>
                        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" >
                            <Navbar.Brand href="/homereal"></Navbar.Brand>
                            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                            <Navbar.Collapse id="responsive-navbar-nav">
                                <Nav className="mr-auto">
                                </Nav>
                                <Nav>
                                    <div className="dropdown">
                                        <button className="dropbtn"><MDBIcon icon="angle-down" /></button>
                                        <div className="dropdown-content">
                                            <a href="/location_gym"></a>
                                            <a href="/add_course"></a>
                                            <a href="/add_course_sup"></a>
                                            <a href="/chack_course"></a>
                                        </div>

                                    </div>
                                    <Nav.Link href="/show_user" ></Nav.Link>
                                    <Nav.Link href="/register"></Nav.Link>
                                    {/* <Nav.Link href="/add_product"></Nav.Link>
                                    <Nav.Link href="/show_product"></Nav.Link>
                                    <Nav.Link href="/add_promotion"></Nav.Link> */}
                                
                                    <Nav.Link href="/user"></Nav.Link>
                                    <Nav.Link  onClick={this.logOut.bind(this)}></Nav.Link>
                                </Nav>
                            </Navbar.Collapse>
                        </Navbar>
                    </div>

                break;

            default:
                render_user =
                    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" >
                        <Navbar.Brand href="/homereal"></Navbar.Brand>
                        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                        <Navbar.Collapse id="responsive-navbar-nav">
                            <Nav className="mr-auto">
                            </Nav>
                            <Nav>
                                <Nav.Link  onClick={this.logOut.bind(this)}></Nav.Link>
                            </Nav>
                        </Navbar.Collapse>
                    </Navbar>
                break;
        }
        return render_user
    }


    logOut = (e) => {
        e.preventDefault()
        localStorage.removeItem('user_token')
        window.location.href = "/";
    }
    render() {

        // 1. 

        const loginUser = (
            <div>
                
                         {this.render_type()}

            </div>

        )
        // 2. 

        const unloginUser = (
            <div></div>
        )
        return (
            <div>
                {localStorage.user_token ? loginUser : unloginUser}
            </div>

        )
    }
}


export default SingnedInLinks;